h1{
    text-align: center;
  }
  
  .lists{
    padding-left: 3%;
    padding-bottom:3%;
  }
  .app{
    display: flex;
    width: 100vw;
    height: 100vh;
    flex-flow: column;
  }
  .header{
    display: flex;
    justify-content: center;
    text-align:center;
    height: 60px;
    margin-bottom: 30px;
  }
  .lists{
    display: flex;
    flex:1;
    width: 100%;
    overflow-x: scroll;
    text-align: center;
  }
  .list{
    display: flex;
    flex-flow: column;
    flex:1;
  
    width:100%;
    min-width: 250px;
    max-width: 350px;
    height: 50%;
    min-height: 50px;
  
    background-color: #96ceb4;
    margin:  0 15px;
    padding: 9px;
    transition: all 0.2s linear;
    margin-top: 30px;
  }
  
  .list-item{
    background-color: yellow;
    border-radius: 8px;
    padding: 15px 20px;
    text-align: center;
    margin: 4px 0px;
    width:10%;
  }
  
  .create{
    padding: 30px;
    margin-bottom: 150px;
  }